<template>
  <div id="MerchandiseNews">
    <el-menu style default-active="0-0-0" class="leftNav" unique-opened>
      <el-submenu
        :index="index + ''"
        v-for="(item, index) in leftNavData"
        :key="index"
      >
        <template slot="title">{{ index }}</template>
        <el-submenu
          :index="index2 + ''"
          v-for="(item2, index2) in item"
          :key="index2"
        >
          <template slot="title">{{ index2 }}</template>
          <el-menu-item
            :index="index2 + item3"
            v-for="(item3, index3) in item2"
            :key="index3"
            @click="NavClick(index, index2, item3)"
            >{{ item3 }}</el-menu-item
          >
        </el-submenu>
      </el-submenu>
    </el-menu>
    <header style="font-size: 16px; line-height: 40px">
      <span style="padding-left: 20px"
        >{{ firstData }}-{{ secondData }}-{{ threeData }}</span
      >
      <el-form :inline="true">
        <el-form-item label="产品编码">
          <el-select v-model="dropProductCode">
            <el-option
              v-for="(item, i) in productCodeList"
              :key="i"
              :value="item.product_code"
              :label="item.product_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="关键字">
          <el-input v-model="searchData" @keyup.enter="search()"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" class="searchBtn" @click="search()"
            >查找</el-button
          >
          <el-button type="primary" @click="clearSearchInfo" class="clearBtn"
            >重置</el-button
          >
        </el-form-item>
      </el-form>
      <!-- <div>
        产品编码：
        <select name="" id="" v-model="dropProductCode">
          <option
            v-for="(item, index) in productCodeList"
            :key="index"
            :value="item.product_code"
          >
            {{ item.product_code }}
          </option>
        </select>
      </div>-->
      <!-- <div>
        关键字：
        <input
          type="text"
          v-model="searchData"
          @keyup.enter="search()"
          style="width: 8vw; height: 3vh"
        />
      </div>-->
      <!-- <div>
        <el-button type="primary" size="small" @click="search()"
          >查找</el-button
        >
      </div>-->
    </header>
    <table style="margin-left: 2vwl" class="MN_table">
      <tr class="title">
        <el-button type="primary" size="small" @click="addNewdata()" class="Btn"
          >新增</el-button
        >
        <el-button size="small" type="primary" @click="openedit()" class="Btn"
          >编辑</el-button
        >
        <el-button type="danger" size="small" @click="deleteData()" class="Btn"
          >删除</el-button
        >
        <el-button size="small" type="primary" class="Btn uploadExcel"
          >上传excel文件<input
            type="file"
            ref="uploadExcel"
            accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
            @change="changeFile"
        /></el-button>
        <!-- <div class="uploadExcel Btn">
          <span>上传文件</span>
          <input type="file" @change="changeFile" />
        </div> -->

        商品信息
      </tr>

      <thead>
        <tr>
          <th></th>
          <th>序号</th>
          <th>国家</th>
          <th>站点</th>
          <th>平台</th>
          <th>产品编码</th>
          <th>spu</th>
          <th>sku</th>
          <th>品名</th>
          <th>类别</th>
          <th>状态</th>
          <th>价格</th>
          <th>Asin</th>
          <th>Fnsku</th>
          <th>Upc</th>
          <th>开始销售时间</th>
          <th>停止销售时间</th>
          <th>操作记录</th>
          <th>产品链接</th>
        </tr>
      </thead>
      <!-- <tbody>
        <tr v-for="(item, index) in tableList" :key="index">
          <td>
            <input
              @change="changeSelect"
              type="checkbox"
              :value="item.id"
              class="checkLI"
            />
          </td>
          <td>{{ index + 1 + (page - 1) * 50 }}</td>
          <td @click="tagCheck(index)">{{ item.country }}</td>
          <td @click="tagCheck(index)">{{ item.site }}</td>
          <td @click="tagCheck(index)">{{ item.platform }}</td>
          <td @click="tagCheck(index)">{{ item.product_code }}</td>
          <td @click="tagCheck(index)">{{ item.spu }}</td>
          <td @click="tagCheck(index)">{{ item.sku }}</td>
          <td @click="tagCheck(index)">{{ item.product_name }}</td>
          <td @click="tagCheck(index)">{{ item.product_type }}</td>
          <td @click="tagCheck(index)">{{ item.commodity_state }}</td>
          <td @click="tagCheck(index)">{{ item.commodity_price }}</td>
          <td @click="tagCheck(index)">{{ item.asin }}</td>
          <td @click="tagCheck(index)">{{ item.fnsku }}</td>
          <td @click="tagCheck(index)">{{ item.upc }}</td>
          <td @click="tagCheck(index)">{{ item.begin_sell_date }}</td>
          <td @click="tagCheck(index)">{{ item.over_sell_date }}</td>
          <td @click="tagCheck(index)">待定</td>
          <td @click="tagCheck(index)">{{ item.product_link }}</td>
        </tr>
      </tbody>-->
      <tbody>
        <tr
          v-for="(item, index) in tableList"
          :key="index"
          @click="clickTr($event, index)"
        >
          <td>
            <!-- @change="changeSelect" -->
            <input type="checkbox" :value="item.id" class="checkLI" />
          </td>
          <td>{{ index + 1 + (page - 1) * 50 }}</td>
          <td>{{ item.country }}</td>
          <td>{{ item.site }}</td>
          <td>{{ item.platform }}</td>
          <td>{{ item.product_code }}</td>
          <td>{{ item.spu }}</td>
          <td>{{ item.sku }}</td>
          <td>{{ item.product_name }}</td>
          <td>{{ item.product_type }}</td>
          <td>{{ item.commodity_state }}</td>
          <td>{{ item.commodity_price }}</td>
          <td>{{ item.asin }}</td>
          <td>{{ item.fnsku }}</td>
          <td>{{ item.upc }}</td>
          <td>{{ item.begin_sell_date }}</td>
          <td>{{ item.over_sell_date }}</td>
          <td>
            <span
              style="
                                display: block;
                                text-decoration: underline;
                                color: blue;
                                z-index: 3;
                                width: 100%;
                                height: 100%;
                                cursor: pointer;
                            "
              v-if="item.check_time"
              class="alink"
              >{{ item.check_time }}</span
            >
            <span
              style="
                                display: block;
                                text-decoration: underline;
                                color: blue;
                                z-index: 3;
                                width: 100%;
                                height: 100%;
                                cursor: pointer;
                            "
              v-else
              >暂无记录</span
            >
          </td>
          <td>{{ item.product_link }}</td>
        </tr>
      </tbody>
    </table>
    <el-pagination
      v-if="pageShow"
      background
      @current-change="handleCurrentChange"
      :current-page="page"
      layout="total,prev, pager, next, jumper"
      :total="total"
      :page-size="50"
      class="pagetotle"
    ></el-pagination>
    <!-- 弹框 -->
    <el-dialog
      title="新增"
      :visible.sync="centerDialogVisible"
      :close-on-click-modal="false"
      width="55%"
      center
    >
      <el-form :inline="true" label-width="100px">
        <el-form-item label="站点">
          <el-input v-model="addSite" readonly></el-input>
        </el-form-item>
        <el-form-item label="国家">
          <el-input v-model="addCountry" readonly></el-input>
        </el-form-item>
        <el-form-item label="平台">
          <el-input v-model="addplatform" readonly></el-input>
        </el-form-item>
        <el-form-item label="产品编码">
          <el-select v-model="addProduct_code">
            <el-option
              v-for="(item, index) in addProduct_codeList"
              :key="index"
              :value="item.product_code"
              :label="item.product_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SPU">
          <el-input v-model="addSpu"></el-input>
        </el-form-item>
        <el-form-item label="SKU">
          <el-input v-model="addSku"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="addState">
            <el-option
              v-for="(item, index) in stateList"
              :key="index"
              :value="item.state"
              :label="item.state"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Asin">
          <el-input v-model="addAsin"></el-input>
        </el-form-item>
        <el-form-item label="Fnsku">
          <el-input v-model="addFnsku"></el-input>
        </el-form-item>
        <el-form-item label="UPC">
          <el-input v-model="addUPC"></el-input>
        </el-form-item>
        <el-form-item label="开始销售时间">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="addStardate"
            type="date"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="停止销售时间">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="addenddate"
            type="date"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="是否跟卖链接">
          <el-select v-model="addsellFlag" @change="addblo()">
            <el-option value="跟卖链接" label="跟卖链接"></el-option>
            <el-option value="原链接" label="原链接"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-show="isShowSell" label="跟卖链接">
          <el-input v-model="addsellLink"></el-input>
        </el-form-item>
        <el-form-item label="产品链接">
          <el-input v-model="addoriginalLink"></el-input>
        </el-form-item>
        <el-form-item class="lastBtnGroup">
          <el-button type="primary" @click="addSubmit()">保 存</el-button>
          <el-button type="warning" @click="addReset()">重 置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog
      title="编辑"
      :visible.sync="centerDialogVisible2"
      :close-on-click-modal="false"
      width="20%"
      center
    >
      <el-form :inline="true" label-width="100px">
        <el-form-item label="站点">
          <el-input v-model="editSite" readonly></el-input>
        </el-form-item>
        <el-form-item label="国家">
          <el-input v-model="editCountry" readonly></el-input>
        </el-form-item>
        <el-form-item label="平台">
          <el-input v-model="editplatform" readonly></el-input>
        </el-form-item>
        <el-form-item label="产品编码">
          <el-select v-model="editProduct_code">
            <el-option
              v-for="(item, index) in editProduct_codeList"
              :key="index"
              :value="item.product_code"
              :label="item.product_code"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="SPU">
          <el-input v-model="editSpu"></el-input>
        </el-form-item>
        <el-form-item label="SKU">
          <el-input v-model="editSku"></el-input>
        </el-form-item>
        <el-form-item label="状态">
          <el-select v-model="editState">
            <el-option
              v-for="(item, index) in stateList"
              :key="index"
              :value="item.state"
              :label="item.state"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="Asin">
          <el-input v-model="editAsin"></el-input>
        </el-form-item>
        <el-form-item label="Fnsku">
          <el-input v-model="editFnsku"></el-input>
        </el-form-item>
        <el-form-item label="UPC">
          <el-input v-model="editUPC"></el-input>
        </el-form-item>
        <el-form-item label="开始销售时间">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="editStardate"
            type="date"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="停止销售时间">
          <el-date-picker
            value-format="yyyy-MM-dd"
            v-model="editenddate"
            type="date"
            placeholder="选择日期"
          ></el-date-picker>
        </el-form-item>
        <el-form-item label="是否跟卖链接">
          <el-select v-model="editsellFlag" @change="editblo()">
            <el-option value="跟卖链接" label="跟卖链接"></el-option>
            <el-option value="原链接" label="原链接"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item v-show="isShowSell1" label="跟卖链接">
          <el-input v-model="editsellLink"></el-input>
        </el-form-item>
        <el-form-item label="产品链接">
          <el-input v-model="editoriginalLink"></el-input>
        </el-form-item>
        <el-form-item class="lastBtnGroup">
          <el-button type="primary" size="small" @click="editSubmit()"
            >提 交</el-button
          >
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      fileList: [],
      file: "",
      pageFlag: false,
      pageShow: true, // 显示分页
      selectItem: "",
      centerDialogVisible2: false,
      centerDialogVisible: false,
      leftNavData: [], //左侧导航数据
      tableList: [], //表格数据
      addSite: "", //新增界面站点
      addCountry: "", //新增界面国家
      addProduct_code: "", //新增界面产品编码
      addSpu: "", //新增界面SPU
      addSku: "", //新增界面SKU
      addState: "", //新增界面状态
      addAsin: "", //新增界面Asin
      addFnsku: "", //新增界面FNSKU
      addUPC: "", //新增界面UPC
      addStardate: "", //新增界面开始售卖时间
      addenddate: "", //新增界面结束售卖时间
      addsellFlag: "", //新增界面是否跟卖select
      addsellLink: "", //新增界面跟卖链接
      addoriginalLink: "", //新增界面原链接
      addplatform: "", //新增界面平台
      editSite: "", //编辑界面站点
      editCountry: "", //编辑界面国家
      editProduct_code: "", //编辑界面产品编码
      editSpu: "", //编辑界面SPU
      editSku: "", //编辑界面SKU
      editState: "", //编辑界面状态
      editAsin: "", //编辑界面Asin
      editFnsku: "", //编辑界面FNSKU
      editUPC: "", //编辑界面UPC
      editStardate: "", //编辑界面开始售卖时间
      editenddate: "", //编辑界面结束售卖时间
      editsellFlag: "", //编辑界面是否跟卖select
      editsellLink: "", //编辑界面跟卖链接
      editoriginalLink: "", //编辑界面原链接
      editplatform: "", //编辑界面平台
      stateList: [], //编辑、新增状态下拉框
      productCodeList: [], //搜索产品编码下拉框
      dropProductCode: "", //搜索产品编码
      firstData: "", //存取左侧一级导航数据
      secondData: "", //存取左侧二级导航数据
      threeData: "", //存取左侧三级导航数据

      total: 0, //总页数
      page: 1, //页码当前页

      addProduct_codeList: [], //新增下拉框产品编码数据
      editProduct_codeList: [], //编辑下拉框产品编码数据

      searchData: "", //搜索关键字,
      isShowSell: false,
      isShowSell1: false
    };
  },
  methods: {
    checkFileType(filename) {
      if (!/\.(xlsx|xls)$/g.test(filename)) {
        return false;
      } else {
        return true;
      }
    },
    changeFile($event) {
      console.log(this.$refs["uploadExcel"]);
      // this.$refs["uploadExcel"].value = "";
      console.log($event);
      let file = $event.target.files[0];
      console.log(file);
      if (!this.checkFileType(file.name)) {
        alert("请上传后缀为.xlsx或.xls文件！");
        this.file = "";
        this.fileList = [];

        return;
      }
      this.$refs["uploadExcel"].value = "";
      this.file = file;
      // 发起请求
      let url = this._url2 + "server/databases/commodity_script/";
      let fd = new FormData();
      // fd.append("platform", this.firstData);
      fd.append("country", this.secondData);
      fd.append("site", this.threeData);
      fd.append("files", this.file);
      this.$axios.post(url, fd).then(res => {
        if (res.data.code === 200) {
          this.$message.success("请求成功");
          this.file = "";
          this.fileList = [];
        } else {
          this.$message.warning("请求失败");
        }
      });
    },
    clearSearchInfo() {
      this.dropProductCode = "";
      this.searchData = "";
      this.search();
    },
    requestData(id) {
      let url = this._url2 + "server/databases/commodity_check_time/";
      this.$axios
        .get(url, {
          params: {
            data_id: id
          }
        })
        .then(res => {
          if (res.data.code === 200) {
            alert("操作成功！");
            this.search(this.page);
          } else {
            alert("操作未成功！");
          }
        });
    },
    clickTr(e, index) {
      let id = this.tableList[index].id;

      if (e.target.constructor === HTMLSpanElement) {
        this.requestData(id);
      } else {
        let all_select = document.querySelectorAll(".checkLI");
        let selectedItem = all_select[index];
        if (this.selectItem) {
          this.selectItem.checked = false;
        }
        if (this.selectItem !== selectedItem) {
          this.selectItem = selectedItem;
          this.selectItem.checked = true;
        } else {
          this.selectItem = "";
        }
      }
    },
    // 选择改变
    // changeSelect(e) {
    //   console.log(e.target, this.selectItem);
    //   let all_select = document.querySelectorAll(".checkLI");
    //   if (this.selectItem) {
    //     // 已选中一个选项，所有选项清空
    //     // for (let i = 0; i < all_select.length; i++) {
    //     //   all_select[i].checked = false;
    //     // }
    //     this.selectItem.checked = false;
    //   }
    //   console.log(this.selectItem === e.target, "点击同一个");
    //   if (this.selectItem !== e.target) {
    //     e.target.checked = true;
    //     this.selectItem = e.target;
    //   } else {
    //     this.selectItem = "";
    //   }
    // },
    getDefaultData() {
      //获取默认数据
      this.firstData = "Amazon";
      this.secondData = "美国";
      this.threeData = "胤佑";
      axios({
        method: "get",
        url: this._url2 + "server/databases/commodity_information",
        params: {
          platform: this.firstData,
          country: this.secondData,
          site: this.threeData,
          page: 1
        }
      }).then(res => {
        console.log(res);
        // this.leftNavData = res.data.left_data_dict;
        this.stateList = res.data.commodity_data;
        this.tableList = res.data.data;
        console.log("打印", this.tableList);
        this.tableList.forEach((item, index) => {
          if (item.commodity_state == "null") {
            this.tableList[index].commodity_state = "";
          }
          if (item.upc == "null") {
            this.tableList[index].upc = "";
          }
          if (item.begin_sell_date == "null") {
            this.tableList[index].begin_sell_date = "";
          }
          if (item.over_sell_date == "null") {
            this.tableList[index].over_sell_date = "";
          }
        });
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.productCodeList = res.data.product_code_data;
        this.addProduct_codeList = res.data.product_code_data;
        this.editProduct_codeList = res.data.product_code_data;
      });
    },
    NavClick(index, index2, item3) {
      //点击左侧导航栏获取表格数据
      console.log(index, index2, item3);
      this.firstData = index;
      this.secondData = index2;
      this.threeData = item3;
      this.dropProductCode = "";
      axios({
        method: "get",
        url: this._url2 + "server/databases/commodity_information",
        params: {
          platform: this.firstData,
          country: this.secondData,
          site: this.threeData,
          page: 1
        }
      }).then(res => {
        console.log(res);
        this.tableList = res.data.data;
        console.log("点击事件", this.tableList);
        this.tableList.forEach((item, index) => {
          if (item.commodity_state == "null") {
            this.tableList[index].commodity_state = "";
          }
          if (item.upc == "null") {
            this.tableList[index].upc = "";
          }
          if (item.begin_sell_date == "null") {
            this.tableList[index].begin_sell_date = "";
          }
          if (item.over_sell_date == "null") {
            this.tableList[index].over_sell_date = "";
          }
        });
        // 表格滚动

        this.$nextTick(() => {
          $(".MN_table >tbody").scrollTop(0);
        });
        this.productCodeList = res.data.product_code_data;
        console.log(res.data);
        console.log(this.productCodeList);
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.page = 1;
        this.searchData = "";
        let checkLI = document.querySelectorAll(".checkLI");
        for (let i = 0; i < checkLI.length; i++) {
          checkLI[i].checked = false;
        }
      });
    },
    addNewdata() {
      //打开新增弹框操作
      this.centerDialogVisible = true;
      console.log(this.tableList);
      this.addplatform = this.firstData;
      this.addCountry = this.secondData;
      this.addSite = this.threeData;
      this.addProduct_code = "";
      this.addSpu = "";
      this.addSku = "";
      this.addState = "";
      this.addAsin = "";
      this.addFnsku = "";
      this.addUPC = "";
      this.addStardate = "";
      this.addenddate = "";
      this.addsellFlag = "";
      this.addsellLink = "";
      this.addoriginalLink = "";
    },
    addReset() {
      //重置操作
      this.addProduct_code = "";
      this.addSpu = "";
      this.addSku = "";
      this.addState = "";
      this.addAsin = "";
      this.addFnsku = "";
      this.addUPC = "";
      this.addStardate = "";
      this.addenddate = "";
      this.addsellFlag = "";
      this.addsellLink = "";
      this.addoriginalLink = "";
    },
    addSubmit() {
      //提交新增
      if (
        this.addSite == "" ||
        this.addCountry == "" ||
        this.addProduct_code == "" ||
        this.addplatform == "" ||
        this.addSpu == "" ||
        this.addSku == "" ||
        this.addState == "" ||
        this.addAsin == "" ||
        this.addFnsku == "" ||
        this.addStardate == "" ||
        this.addsellFlag == "" ||
        this.addoriginalLink == ""
      ) {
        alert("请填写除UPC和停止销售时间外所有选项");
      } else {
        this.centerDialogVisible = false;
        let formData = new FormData();
        formData.append("site", this.addSite);
        formData.append("country", this.addCountry);
        formData.append("product_code", this.addProduct_code);
        formData.append("platform", this.addplatform);
        formData.append("spu", this.addSpu);
        formData.append("sku", this.addSku);
        formData.append("commodity_state", this.addState);
        formData.append("asin", this.addAsin);
        formData.append("fnsku", this.addFnsku);
        formData.append("upc", this.addUPC);
        formData.append("begin_sell_date", this.addStardate);
        formData.append("over_sell_date", this.addenddate);
        formData.append("select_link", this.addsellFlag);
        formData.append("sku_link", this.addsellLink);
        formData.append("product_link", this.addoriginalLink);
        this.$axios
          .post(this._url2 + "server/databases/commodity_information", formData)
          .then(res => {
            if (res.data.code == 200) {
              alert("新增成功");
              // 需要额外处理
              this.reloadTable("新增");
            } else {
              alert(res.data.msg);
            }
          });
      }
    },
    deleteData() {
      //删除操作
      let demo = [];
      let checkLI = document.querySelectorAll(".checkLI");
      for (let i = 0; i < checkLI.length; i++) {
        if (checkLI[i].checked == true) {
          demo.push(checkLI[i].value);
          // demo = checkLI[i].value
        }
      }
      // console.log(demo)
      if (demo.length === 0) {
        alert("请选中您想删除的数据");
      } else {
        this.$confirm("此操作将永久删除选中商品, 是否继续?", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        })
          .then(() => {
            this.$axios
              .delete(this._url2 + "server/databases/commodity_information", {
                params: {
                  id: demo
                }
              })
              .then(res => {
                if (res.data.code == 200) {
                  // this.reloadEditTable();
                  // 是否需要处理异常
                  this.reloadTable("删除");
                  this.$message({
                    type: "success",
                    message: "删除成功!"
                  });
                  let checkLI = document.querySelectorAll(".checkLI");
                  for (let i = 0; i < checkLI.length; i++) {
                    checkLI[i].checked = false;
                  }
                }
              });
          })
          .catch(() => {});
      }
    },
    editSubmit() {
      //保存编辑
      if (
        this.editSite == "" ||
        this.editCountry == "" ||
        this.editProduct_code == "" ||
        this.editplatform == "" ||
        this.editSpu == "" ||
        this.editSku == "" ||
        this.editState == "" ||
        this.editAsin == "" ||
        this.editFnsku == "" ||
        this.editStardate == "" ||
        this.editsellFlag == "" ||
        this.editoriginalLink == ""
      ) {
        alert("请填写除UPC和停止销售时间外所有选项");
      } else {
        this.centerDialogVisible2 = false;
        let id = "";
        let checkLI = document.querySelectorAll(".checkLI");
        for (let i = 0; i < checkLI.length; i++) {
          if (checkLI[i].checked == true) {
            id = checkLI[i].value;
          }
        }
        let formData = new FormData();
        formData.append("id", id);
        formData.append("site", this.editSite);
        formData.append("country", this.editCountry);
        formData.append("product_code", this.editProduct_code);
        formData.append("platform", this.editplatform);
        formData.append("spu", this.editSpu);
        formData.append("sku", this.editSku);
        formData.append("commodity_state", this.editState);
        formData.append("asin", this.editAsin);
        formData.append("fnsku", this.editFnsku);
        formData.append("upc", this.editUPC);
        formData.append("begin_sell_date", this.editStardate);
        formData.append("over_sell_date", this.editenddate);
        formData.append("select_link", this.editsellFlag);
        formData.append("sku_link", this.editsellLink);
        formData.append("product_link", this.editoriginalLink);
        this.$axios
          .put(this._url2 + "server/databases/commodity_information", formData)
          .then(res => {
            if (res.data.code == 200) {
              alert("修改成功");
              console.log(this.page);
              // this.reloadEditTable();
              this.reloadTable("编辑");
            }
          });
      }
    },
    openedit() {
      //打开编辑
      let checkLI = document.querySelectorAll(".checkLI");
      let flag = 0;

      for (let i = 0; i < checkLI.length; i++) {
        if (checkLI[i].checked == true) {
          flag++;
          this.editProduct_code = this.tableList[i].product_code;
          this.editSpu = this.tableList[i].spu;
          this.editSku = this.tableList[i].sku;
          this.editAsin = this.tableList[i].asin;
          this.editFnsku = this.tableList[i].fnsku;
          this.editUPC = this.tableList[i].upc;
          this.editStardate = this.tableList[i].begin_sell_date;
          this.editenddate = this.tableList[i].over_sell_date;
          this.editoriginalLink = this.tableList[i].product_link;
          this.editState = this.tableList[i].commodity_state;
          this.editsellFlag = this.tableList[i].select_link;
        }
      }
      if (flag == 1) {
        this.centerDialogVisible2 = true;
        this.editSite = this.tableList[0].site;
        this.editCountry = this.tableList[0].country;
        this.editplatform = this.tableList[0].platform;
        console.log(this.tableList);
      } else {
        alert("请只选择一个编辑");
      }
    },
    search(page) {
      //搜索操作
      if (!page) {
        this.page = 1;
      }
      axios({
        method: "get",
        url: this._url2 + "server/databases/commodity_information",
        params: {
          platform: this.firstData,
          country: this.secondData,
          site: this.threeData,
          product_code: this.dropProductCode,
          condition: this.searchData,
          page: page || this.page
        }
      }).then(res => {
        console.log(res);
        // this.page = 1;
        this.tableList = res.data.data;
        // this.total = res.data.count_data[0].count_id / 5;
        // this.total = res.data.count_data[0].count_id;
        let checkLI = document.querySelectorAll(".checkLI");
        for (let i = 0; i < checkLI.length; i++) {
          checkLI[i].checked = false;
        }
      });
    },
    reloadEditTable() {
      this.page = 1;
      //其他函数调用用于重新刷新页面
      axios({
        method: "get",
        url: this._url2 + "server/databases/commodity_information",
        params: {
          platform: this.firstData,
          country: this.secondData,
          site: this.threeData,
          product_code: this.dropProductCode,
          page: this.page
        }
      }).then(res => {
        this.tableList = res.data.data;
        this.total = res.data.count_data[0].count_id;
      });
    },
    reloadTable(val) {
      // 判断是否是最后一条，如果是则页数加一
      // this.page = 1;
      //其他函数调用用于重新刷新页面

      if (val && val === "新增") {
        // 直接跳到最后一页
        console.log("总条数", this.total);
        let lastPage = Math.ceil((this.total + 1) / 50);
        if (Math.ceil(this.total / 50) !== lastPage) {
          this.pageShow = false;
          this.pageFlag = true;
        }
        this.page = lastPage;
      } else if (val && val === "删除") {
        // 删除
        let lastTotal = this.total - 1;
        console.log("删除后条数", lastTotal);
        console.log("总条数", this.total);
        if (lastTotal < 50 * this.page) {
          // 删除后条数 小于当前页数
          this.page = Math.ceil(lastTotal / 50);
        }
      }
      axios({
        method: "get",
        url: this._url2 + "server/databases/commodity_information",
        params: {
          platform: this.firstData,
          country: this.secondData,
          site: this.threeData,
          product_code: this.dropProductCode,
          page: this.page
          // 修改测试
          // condition: this.searchData
        }
      }).then(res => {
        console.log(res);
        this.tableList = res.data.data;
        // this.total = res.data.count_data[0].count_id / 5;
        this.total = res.data.count_data[0].count_id;
        this.$nextTick(() => {
          if (this.pageFlag) {
            this.pageShow = true;
          }
        });
      });
    },
    addblo() {
      //跟卖下拉框的改变
      console.log(this.addsellFlag);
      if (this.addsellFlag == "跟卖链接") {
        this.isShowSell = true;

        // document.querySelector(".hidBlo1").style.display = "block";
        // document.querySelector(".hidBlo2").style.display = "block";
      } else {
        this.isShowSell = false;
        // document.querySelector(".hidBlo1").style.display = "none";
        // document.querySelector(".hidBlo2").style.display = "none";
      }
    },
    editblo() {
      //跟卖下拉框改变
      if (this.editsellFlag == "跟卖链接") {
        // document.querySelector(".editblo1").style.display = "block";
        // document.querySelector(".editblo2").style.display = "block";
        this.isShowSell1 = true;
      } else {
        this.isShowSell1 = false;
        // document.querySelector(".editblo1").style.display = "none";
        // document.querySelector(".editblo2").style.display = "none";
      }
    },
    tagCheck(index) {
      //表格全部复选框
      // document.querySelectorAll('.checkLI')[index].checked = 'true'
      if (document.querySelectorAll(".checkLI")[index].checked == false) {
        document.querySelectorAll(".checkLI")[index].checked = true;
      } else {
        document.querySelectorAll(".checkLI")[index].checked = false;
      }
    },
    handleCurrentChange(newPage) {
      // 页码切换会触发此函数
      this.page = newPage;
      axios({
        method: "get",
        url: this._url2 + "server/databases/commodity_information",
        params: {
          platform: this.firstData,
          country: this.secondData,
          site: this.threeData,
          page: this.page,
          condition: this.searchData,
          product_code: this.dropProductCode
        }
      }).then(res => {
        console.log(res);
        this.tableList = res.data.data;

        this.$nextTick(() => {
          $(".MN_table >tbody").scrollTop(0);
        });

        let checkLI = document.querySelectorAll(".checkLI");
        for (let i = 0; i < checkLI.length; i++) {
          checkLI[i].checked = false;
        }
      });
    },
    addEnter() {
      this.addSubmit();
    },
    editEnter() {
      this.editSubmit();
    }
  },
  created() {
    this.getDefaultData();
    axios({
      //左侧导航栏（权限用）
      url: this._url2 + "server/personnel_management/area_sign2/",
      method: "get"
    }).then(res => {
      this.leftNavData = res.data.data;
      console.log(this.leftNavData);
      let flag = 0;

      for (let key in this.leftNavData.Amazon) {
        if (
          key == "英国" ||
          key == "德国" ||
          key == "意大利" ||
          key == "西班牙" ||
          key == "法国"
        ) {
          flag++;
        }
      }
      if (flag != 0) {
        let demo = [];

        for (let key in this.leftNavData.Amazon) {
          if (
            key == "英国" ||
            key == "德国" ||
            key == "意大利" ||
            key == "西班牙" ||
            key == "法国"
          ) {
            for (let i = 0; i < this.leftNavData.Amazon[key].length; i++) {
              demo.push(this.leftNavData.Amazon[key][i]);
            }
          }
          console.log(this.leftNavData.Amazon[key]);
        }
        demo = [...new Set(demo)];
        this.leftNavData.Amazon["欧洲"] = demo;
        if (this.leftNavData.Amazon.英国) {
          delete this.leftNavData.Amazon.英国;
        }
        if (this.leftNavData.Amazon.德国) {
          delete this.leftNavData.Amazon.德国;
        }
        if (this.leftNavData.Amazon.意大利) {
          delete this.leftNavData.Amazon.意大利;
        }
        if (this.leftNavData.Amazon.西班牙) {
          delete this.leftNavData.Amazon.西班牙;
        }
        if (this.leftNavData.Amazon.法国) {
          delete this.leftNavData.Amazon.法国;
        }
      }
    });
  },
  mounted() {
    window.addEventListener("scroll", this.handleScroll);
  }
};
</script>
<style>
.el-pagination {
  background-color: #fff;
  margin: 0 auto;
  width: 600px;
  text-align: center;
}
</style>
<style scoped>
#MerchandiseNews table {
  /* overflow: scroll; */
  /*width: 84vw;*/
  margin: 10px 0px;
  border-collapse: collapse;
  margin-left: 12.5vw;
  background: #fff !important;
}

tbody {
  display: block;
  max-height: 68vh;
  width: fit-content;
  overflow-y: auto;
}

thead,
tbody tr {
  display: table;
  width: fit-content;
  table-layout: fixed;
}

/*th和td的宽度使用vw或者px*/
thead th,
tbody tr td {
  border: 1px solid #e5e5e5;
  border-top: none;
  text-align: center;
  width: 8vw;
}

table thead tr {
  border-top: 1px solid #e5e5e5;
  background-color: #f7f5f5;
}

table tbody tr:hover {
  background: #eaf7e0;
}

table thead tr th {
  font-size: 0.9rem;
  color: #000;
  font-weight: 600;
  /* height: 2.5vh;
  line-height: 2.5vh; */
}

table tbody tr td {
  font-size: 0.7rem;
  color: #000;
  /* height: 2.5vh;
  line-height: 2.5vh; */
}
/* .testDiv::-webkit-scrollbar {
  width: 0 !important;
  height: 0 !important;
} */
/* 设置滚动条的大小 */
::-webkit-scrollbar {
  width: 9px;
  height: 9px;
}

.title {
  border-top: #e5e5e5 1px solid;
  border-left: #e5e5e5 1px solid;
  border-right: #e5e5e5 1px solid;
  height: 40px;
  line-height: 40px;
  text-align: center;
  text-indent: -25vw;
  font-size: 1.5rem;
}

table tr td:nth-child(1),
table tr th:nth-child(1),
table tr td:nth-child(2),
table tr th:nth-child(2) {
  width: 2vw;
}

table tr td:nth-child(3),
table tr th:nth-child(3),
table tr td:nth-child(4),
table tr th:nth-child(4),
table tr td:nth-child(10),
table tr th:nth-child(10),
table tr td:nth-child(11),
table tr th:nth-child(11),
table tr td:nth-child(12),
table tr th:nth-child(12) {
  width: 3vw;
}

table tr td:nth-child(5),
table tr th:nth-child(5),
table tr td:nth-child(6),
table tr th:nth-child(6),
table tr td:nth-child(7),
table tr th:nth-child(7),
table tr td:nth-child(8),
table tr th:nth-child(8),
table tr td:nth-child(13),
table tr th:nth-child(13),
table tr td:nth-child(14),
table tr th:nth-child(14),
table tr td:nth-child(15),
table tr th:nth-child(15),
table tr td:nth-child(16),
table tr th:nth-child(16),
table tr td:nth-child(17),
table tr th:nth-child(17),
table tr td:nth-child(18),
table tr th:nth-child(18) {
  width: 5vw;
}

table tr td:nth-child(9),
table tr th:nth-child(9) {
  width: 10vw;
}

.dialogSpan {
  display: flex;
}

.dialogLeft {
  flex: 3;
}

.dialogRight {
  flex: 7;
}

.dialogLeft div {
  text-align: right;
  height: 4vh;
  font-size: 0.8rem;
  line-height: 3vh;
}

.dialogRight div {
  height: 4vh;
}

.dialogRight textarea {
  height: 9vh;
}

.dialogRight select,
.dialogRight input {
  height: 3vh;
  width: 10vw;
}

header {
  width: 60vw;
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 10px;
  display: flex;
  font-size: 16px;
  line-height: 40px;
  justify-content: space-between;
  padding: 5px 30px;
}

header div {
  flex: 1;
  margin: auto;
  text-align: center;
}

header select {
  width: 8vw;
  height: 3vh;
}

header button {
  width: 8vw;
}

.hidBlo1,
.hidBlo2 {
  display: none;
}

.editblo1,
.editblo2 {
  display: none;
}

.Btn {
  float: left;
  width: 100px;
  margin: 5px;
}
.searchBtn,
.clearBtn {
  height: 40px;
  width: 100px;
}
.leftNav {
  width: 12vw;
  height: 100vh;
  position: fixed;
  left: 0;
}
/* .tableDiv {
  max-width: 88vw;
  overflow-x: auto;
} */
/* 新增 */
table > tbody > tr > td {
  white-space: break-spaces;
  word-break: break-word;
}
#MerchandiseNews .alink {
  text-decoration: underline;
  color: blue;
}
.uploadExcel {
  position: relative;
  /* font-size: 14px;
  color: #fff;
  background: blue;
  height: 32px;
  border-radius: 4px; */
}
.uploadExcel input[type="file"] {
  position: absolute;
  top: 0;

  left: 0;
  opacity: 0;
  width: 100%;
  height: 100%;
}
</style>
<style>
#MerchandiseNews .el-form .el-input {
  width: 202px;
}
#MerchandiseNews .el-form {
  text-align: right;
  margin-right: 20px;
}
#MerchandiseNews .lastBtnGroup {
  width: 100%;
  text-align: center;
}
</style>



